*{
  margin: 0;
}

body {
  margin: 0;
  background-color: rgb(234,237,237);
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.header{
    height : 60px ; 
    display: flex ; 
    align-items: center;
    background-color: #131921;
    position : -webkit-sticky ;
    position : sticky ; 
    top: 0 ; 
    z-index : 99999 ; 
}

.header_logo{
    width : 100px ; 
    object-fit: contain; 
    margin : 0 20px ; 
    margin-top : 18px ; 
}

.header_search{
    display : flex ; 
    flex : 1 1; 
    align-items : center ; 
    border-radius : 24px ; 
}

.header_searchInput{
    height : 14px  ; 
    padding : 10px ; 
    border: none;
    width : 100% ; 
}

.header_searchIcon{
    padding : 5px ; 
    height : 22px ; 
    background-color: #cd9842;
}

.header_nav{
    display: flex;
    justify-content: space-evenly;
    color : white ; 
}

.header_option{
    display : flex ; 
    flex-direction: column;
    margin-left: 10px ; 
    margin-right: 10px;
}

.header_optionLineOne{
    font-size: 10px ;
}

.header_optionLineTwo{
    font-size : 13px ; 
    font-weight : 800 ; 
}

.header_optionBasket{
    display : flex ; 
    align-items : center ; 
}

.header_basketCount {
    margin-left: 10px;
    margin-right: 10px;
}
.home_container {
    display : flex ; 
    justify-content: center;
    margin-left : auto ; 
    margin-right : auto ; 
    max-width : 1500px ;  
}

.home_row{
    display : flex ; 
    z-index : 1 ; 
    margin-left : 5px ; 
    margin-right : 5px ; 
}
.home_image{
    width : 100% ; 
    z-index: -1 ; 
    margin-bottom : -150px ; 
    -webkit-mask-image : linear-gradient(to bottom, rgba(0,0,0,1), rgba(0,0,0,0)); 
            mask-image : linear-gradient(to bottom, rgba(0,0,0,1), rgba(0,0,0,0))
}
.product{
    z-index: 1 ;
    display: flex;
    flex-direction: column ;
    background-color: white;
    z-index: 1;
    align-items: center;
    justify-content: flex-end;
    margin : 10px ; 
    padding : 20px ; 
    width : 100% ; 
    max-height : 400px ; 
    min-width: 100px;
}

.product > img{
    max-height : 200px ; 
    width : 100% ; 
    object-fit : contain ; 
    margin-bottom: 15px  ;
    justify-content: left ; 
}

.product_info{
    height : 100px ; 
    width : 100% ; 
    margin-bottom: 15px;
}

.product_info > p {
    text-align : left  ; 
}

.product > button {
    background-color: #f0c14b;
    border : 1px solid ; 
    margin-top : 10px ; 
    border-color : #a88734 #9c7e31 #846a29 ; 
    color : #111 ; 
}

.product_rating {
    display: flex;

}
.checkout {
    display : flex ; 
    padding : 20px ; 
    background-color : white ; 
    height : -webkit-max-content ; 
    height : max-content ;
}
.checkout_ad{
    width : 100% ; 
    margin-bottom : 10px ;  
}

.checkout_title {
    margin-right : 10px ; 
    padding : 10px ; 
    border-bottom : 1px solid lightgray ; 
}


.subtotal{
    display: flex;
    flex-direction: column;
    justify-content : space-between ; 
    width : 300px ; 
    height : 100px ; 
    padding : 20px ; 
    background-color: #f3f3f3;
    border : 1px solid #dddddd ; 
    border-radius: 3px;
}

.subtotal_gift{
    display : flex ; 
    align-items : center ; 
}

.subtotal_gift > input {
    margin-right : 5px ; 
}

.subtotal > button{
    background : #f0c14b ; 
    border-radius : 2px ; 
    width : 100% ; 
    height : 30px ; 
    border: 1px solid;
    margin-top : 10px ; 
    border-color : #a88734#9c7e31 #846a29 ; 
    color : #111 ; 
}
